:root {
    --primaryColor: #52616b;
    --primaryFontFamily: 'Source Serif Pro', serif;
}

body {
    font-family: var(--primaryFontFamily);
    background-color: #f0f5f9;
}

input:focus,
textarea:focus {
    border-color: var(--primaryColor) !important;
    box-shadow: 0 0 0 0.2rem rgba(82, 97, 107, 0.582) !important;
}

/* Header */
/* SideMenu */

.navbar {
    background-color: #c9d6df;
    box-shadow: 0px 5px 5px #c4c4c4;
    height: 120px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.navbar-brand {
    color: #52616b !important;
    font-weight: 600;
    font-size: 40px;
    margin: 5px 16px 0 50px;
    padding: 0 !important;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2.5rem;
    background: none;
}

.menu-close {
    margin-left: 0rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #c9d6df;
    width: 380px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 2;
}

.nav-menu.active {
    box-shadow: 0px 5px 5px #c4c4c4;
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 0px 8px 0px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #52616b;
    font-size: 24px;
    width: 200;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text-brand {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 0px 8px 0px;
    list-style: none;
    height: 60px;
}

.nav-text-brand a {
    text-decoration: none;
    color: #52616b;
    font-size: 32px;
    font-weight: 600;
    width: 200;
    height: 150;
    display: flex;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 50px;
    border-radius: 4px;
}

.nav-menu-items {
    padding-right: 60px;
    align-items: center;
}

.component-app {

    width: 30%;
    margin-left: 3%;
    height: 614px;

    margin-top: 286px;

    border-radius: 10px;
    background: #C9D6DF;
    border: 1px solid #000000;
    box-shadow: 2px 2px 8px 6px rgba(0, 0, 0, 0.25);
    overflow: hidden;
}

@media only screen and (max-width: 411px) {
    .component-app {
        margin-left: 5%;
        width: 260px;
        margin-top: 260px;
        height: 376px;
    }
    .component-button {
        display: inline-flex;
        width: 64.84px;
        height: 48.38px;
        flex: 1 0 auto;
      }
      .component-button.wide {
        width: 50.3%;
      }
      .component-button button {
        font-weight: 500;
        font-size: 18px;
        
      }
      .result {
        font-size: 48px;
        padding: 0.15rem 0.3rem 0.0rem;
      }
}

@media only screen and (min-width: 600px) {
    .component-app {
      margin-left: 10%;
      width: 30%;
      height: 614px;
    }
}

@media only screen and (min-width: 720px) {
    .component-app {
      margin-left: 20%;
      width: 30%;
      height: 614px;
    }
}
  
@media only screen and (min-width: 968px) {
    .component-app {
      margin-left: auto;
      margin-right: auto;
      width: 30%;
      height: 614px;
      
    }
}



@media (max-width: 700px) {
    /* header */
.navbar-toggle {
    background-color: #c9d6df;
    width: 100%;
    height: 80px;
    display: flex;
    padding-top: 70px;
    padding-bottom: 70px;
    padding-left: 240px;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}
}

@media only screen and (min-width: 300px) {
    .navbar-brand {
        font-size: 22px !important;
    }

    .nav-menu-items {
        padding-right: 0;
    }

    .nav-menu {
        left: -120%;
    }

    .nav-menu.active {
        left: -50px !important;
    }

    .menu-bars {
        font-size: 1.5rem;
    }

    .navbar {
        height: 100px;
    }

    .nav-text a {
        font-size: 18px;
    }

    .nav-text-brand a {
        font-size: 25px;
    }

    .nav-menu {
        width: 350px;
    }
}

@media (max-width: 600px) {
    .navbar-brand {
        font-size: 26px;
    }
}

